.wrapper {
    display: flex;
    align-items: center;
    column-gap: 1%;
    padding-bottom: 1%;
}
.wrapper img {
    width: 60px;
    height: 60px;
    border-radius: 65% 65% 60% 60%;
    object-fit: cover;
}

.text {
    font-size: 1em;
    color: #282c34;
    text-decoration: underline;
}